export class User {
    user_name: string;
    user_unit: string;
    phone: string;
    area: string;
    auth: string;
    account: string;
    password: string;

    public checkInput() {
        // TODO : 
    }

    public getObject() {
        let _auth = (this.auth == "") ? "admin" : "user";
        let object = {
            user_name: this.user_name,
            user_unit: this.user_unit,
            phone: this.phone,
            area: this.area,
            auth: _auth,
            account: this.account,
            password: this.password
        };

        return object;
    }

    pushData(ob) {
        this.user_name = ob.user_name;
        this.user_unit = ob.user_unit;
        this.phone = ob.phone;
        this.area = ob.area;
        this.auth = (ob.auth == "") ? "admin" : "user";
        this.account = ob.account;
        this.password = ob.password;
        console.log(ob);
    }
}

export const PrimaryKey = 'account';
export let CategorySearch = ['', '', '', ''];
export let CategoryKey = ['', 'user_name', 'area', 'account'];
export let Area = ['', '', ''];
export let Auth = ['', '', ''];


