import { Component, OnInit } from '@angular/core';

@Component({
    selector: 'page-donor',
    templateUrl: `./page-donor.component.html`
})
export class PageDonorComponent implements OnInit {
    selectedBut = 'search';
    ngOnInit(): void { }

    butClick(_selectedBut: string) {
        this.selectedBut = _selectedBut;
    }
}



