export class Donee {
    donee_name: string;
    phone: string;
    contact_phone: string;
    email: string;
    address: string;
    contact: string;
    category: string;
    house_num: number;
    people_num: number;

    public checkInput() {
        // TODO : 
    }

    public getObject() {
        let object = {
            donee_name: this.donee_name,
            phone: this.phone,
            contact_phone: this.contact_phone,
            email: this.email,
            address: this.address,
            contact: this.contact,
            category: this.category,
            house_num: this.house_num,
            people_num: this.people_num
        };

        return object;
    }

    pushData(ob) {
        this.donee_name = ob.donee_name;
        this.phone = ob.phone;
        this.contact_phone = ob.contact_phone;
        this.email = ob.email;
        this.address = ob.address;
        this.contact = ob.contact;
        this.category = ob.category;
        this.house_num = ob.house_num;
        this.people_num = ob.people_num;
    }
}

export const PrimaryKey = 'donee_name';
export let CategorySearch = ['', '', ''];
export let CategoryKey = ['', 'donee_name', 'category'];
export let Category = ['', '', ' | ', ' | ',
    ' -  |  |  |  | ', ' ()'];


